import { NavLink } from "remix";

export const Navbar = () => {
  return (
    <header>
      <nav>
        <div className="nav_top">
          <button>
            <i className="fa-solid fa-bars"></i>
          </button>
        </div>
        <ul className="nav_list">
          <li className="nav_list_item">
            <NavLink to="/">
              <i class="fa-solid fa-house"></i> Home
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/characters">
              <i class="fa-solid fa-person"></i> Characters
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/locations">
              <i class="fa-solid fa-location-dot"></i> Locations
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/story">
              <i class="fa-solid fa-pen-line"></i> Story
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/about">
              <i class="fa-solid fa-address-card"></i> About
            </NavLink>
          </li>
          <li className="nav_list_item">
            <NavLink to="/contact">
              <i class="fa-solid fa-address-book"></i> Contact
            </NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
};
